const units = {
  second: 1e3,
  minute: 6e4,
  hour: 36e5,
  day: 864e5
};
const duration = ({
  then,
  now
}) => {
  const difference = now - then;
  if (difference < units.minute) {
    return { length: difference / units.second, unit: "second" };
  } else if (difference < units.hour) {
    return { length: difference / units.minute, unit: "minute" };
  } else if (difference < units.day) {
    return { length: difference / units.hour, unit: "hour" };
  } else {
    return { length: difference / units.day, unit: "day" };
  }
};
const isYesterday = (then, now) => {
  const today = new Date(now);
  const yesterday = new Date(now);
  yesterday.setDate(today.getDate() - 1);
  return new Date(then).toDateString() === yesterday.toDateString();
};
const withTime = (date) => `${date.getHours()}.${date.getMinutes().toString().padStart(2, "0")}`;
const timeAgo = (epoch, options) => {
  const then = epoch;
  const now = options?.now ?? Date.now();
  const verbose = options?.verbose ?? false;
  const { length: rawLength, unit } = duration({ then, now });
  const length = Math.round(rawLength);
  if (length < 0) {
    return false;
  }
  switch (unit) {
    case "second": {
      if (length > 55) {
        return verbose ? "1 minute ago" : "1m ago";
      } else if (length < 15) {
        return "now";
      } else if (!verbose) {
        return `${length}s ago`;
      } else {
        return `${length} seconds ago`;
      }
    }
    case "minute": {
      if (length > 55) {
        return verbose ? "1 hour ago" : "1h ago";
      } else if (!verbose) {
        return `${length}m ago`;
      } else if (length == 1) {
        return "1 minute ago";
      } else {
        return `${length} minutes ago`;
      }
    }
    case "hour": {
      if (!verbose) {
        return `${length}h ago`;
      } else if (length == 1) {
        return "1 hour ago";
      } else {
        return `${length} hours ago`;
      }
    }
    case "day": {
      if (rawLength < (options?.daysUntilAbsolute ?? 7)) {
        if (!verbose) {
          return `${length}d ago`;
        } else if (isYesterday(then, now)) {
          return `Yesterday ${withTime(new Date(then))}`;
        } else if (length == 1) {
          return "1 day ago";
        } else {
          return `${length} days ago`;
        }
      }
      return new Date(then).toLocaleString("en-GB", {
        day: "numeric",
        month: verbose ? "long" : "short",
        year: "numeric"
      });
    }
  }
};

export { duration, timeAgo };
